export default {
  data() {
    return {
      userInfo: null,
      showEditForm: false,
      editedUserInfo: {}
    };
  },
  async created() {
    // 这里的 id 应该从登录后的响应中获取
    const id = 'your_id_here';
    try {
      const response = await api.getUserInfo(id);
      this.userInfo = response.data;
      this.editedUserInfo = {
        ...this.userInfo
      }; // 初始化编辑表单的数据
    } catch (error) {
      console.error('获取用户信息失败', error);
    }
  },
  methods: {
    async submitForm() {
      try {
        await api.updateUserInfo(this.editedUserInfo);
        this.userInfo = {
          ...this.editedUserInfo
        }; // 更新当前显示的用户信息
        this.showEditForm = false; // 隐藏编辑表单
      } catch (error) {
        console.error('更新用户信息失败', error);
      }
    }
  },
  methods: {
    async submitForm() {
      // ... 发送更新后的用户信息
      this.isEditing = false;
    }
  }
};